.tabs {
  max-width: 100%;
}

.tabs__controls {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 50px;
  flex-wrap: wrap;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
}
@media (max-width: 1199px) {
  .tabs__controls {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .tabs__controls {
    font-size: 16px;
    line-height: 24px;
  }
}

.tabs__controls-wrapper {
  position: relative;
}
.tabs__controls-wrapper::before {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  background-color: rgba(120, 120, 120, 0.2);
  left: 0;
  right: 0;
  bottom: -18px;
}
@media (max-width: 1199px) {
  .tabs__controls-wrapper::before {
    bottom: -17px;
  }
}

.tabs__control {
  position: relative;
  cursor: pointer;
  min-width: auto;
  justify-content: center;
  width: auto;
  padding: 0;
  border: 0;
  background-color: rgba(255, 255, 255, 0);
  color: rgba(16, 38, 84, 0.5);
}
.tabs__control:hover, .tabs__control:focus {
  color: rgba(16, 38, 84, 0.7);
}

.tabs__control:not(:last-child) {
  margin-right: 46px;
}
@media (max-width: 1199px) {
  .tabs__control:not(:last-child) {
    margin-right: 54px;
  }
}
@media (max-width: 767px) {
  .tabs__control:not(:last-child) {
    margin-right: 42px;
  }
}

.tabs__control.is-active {
  pointer-events: none;
  color: #102654;
}
.tabs__control.is-active::before {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  background-color: #d10a42;
  left: 0;
  right: 0;
  bottom: -20px;
}

.tabs__content {
  transition: height 0.3s;
}

.tabs__element {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
}
@media (max-width: 1199px) {
  .tabs__element {
    grid-template-columns: 66.08%;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .tabs__element {
    grid-template-columns: 1fr;
  }
}

.tabs__element-item {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  padding: 68px 20px;
  border: 7px solid rgba(255, 255, 255, 0);
