  font-size: 26px;
  line-height: 29px;
  text-align: center;
}
@media (max-width: 767px) {
  .tabs__element-item-title {
    font-size: 22px;
    line-height: 24px;
  }
}

.tabs__element-item-info {
  margin-bottom: 90px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #495a7d;
}

.tabs__element-item-price {
  position: relative;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  text-align: center;
  color: #103aae;
  z-index: 2;
}
@media (max-width: 767px) {
  .tabs__element-item-price {
    font-size: 48px;
    line-height: 72px;
  }
}
.tabs__element-item-price:before {
  position: absolute;
  content: attr(data-price-shadow);
  display: block;
  top: -90%;
  left: -10%;
  color: rgba(232, 232, 232, 0.48);
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  z-index: -1;
}
@media (max-width: 767px) {
  .tabs__element-item-price:before {
    font-size: 80px;
    line-height: 120px;
  }
}

.tabs__element-item-price-wrapper {
  text-align: center;
}
@media (max-width: 767px) {
  .tabs__element-item-price-wrapper {
    margin-bottom: 40px;
  }
}

.tabs__element-item-symbol {
  color: #102654;
}

.tabs__element-item-button {
  border: 2px solid #102654;
  color: #102654;
  min-width: 206px;
}
.tabs__element-item-button:hover, .tabs__element-item-button:focus {
  opacity: 0.6;
}
.tabs__element-item-button:active {
  opacity: 0.4;
}

.tabs__element-item:hover .tabs__element-item-button {
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003Eb7 95.31%);
  border: 0;
  color: #ffffff;
}

.tabs__element-item:hover {
  border: 7px solid;
  border-image: linear-gradient(135deg, #d10a42, #00359f) 25;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

